@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
html, body, .Login-component {
  height: 100%;
  background-image: linear-gradient(to top, rgba(41, 44, 46, 0.938), rgba(30, 35, 36, 0.1)), url(/Login-Page/static/media/login.9a76e74e.svg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}


.fieldset{background-color:gray;margin:100px;width:450px;margin-left:1px;border:2px #2b2828 dashed;border-radius:5px}.base-container{width:100%;display:flex;flex-direction:column;align-items:center}.header{font-size:3em;font-family:"Open Sans", sans-serif}.login{align-items:center;display:flex;margin-left:130px;font-family:"Open sans";color:#423b3b;font-weight:bold}.content{display:flex;flex-direction:column}.content .image{width:21em}.content .image img{width:100%;height:100%}.content .form{margin-top:2em;display:flex;flex-direction:column;align-items:center}.content .form-group{display:flex;align-items:flex-start;flex-direction:column;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.content .form-group label{font-size:20px}.content .form-group input{margin-top:6px;min-width:18em;height:37px;padding:0px 10px;border-radius:4px;background-color:#e7e3e3;font-size:16px;font-family:"Open sans", sans-serif;border:none;margin-bottom:30px;transition:all 250ms ease-in-out}.content .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #0e81ce96}.forgot-password{color:#c41313;cursor:pointer}.footer{margin-top:3em}.footer .btn{font-size:25px;padding:2px 10px;margin-bottom:20px;height:40px;width:100px;border:0;background-color:#17638f;color:#fff;border-radius:4px;transition:all 250ms ease-in-out;cursor:pointer}.footer .btn:hover{background-color:#7eafcf;color:black}.footer .btn:focus{outline:none}

/*custom fonts*/

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Open Sans", sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



//Login Main Container
.login {
  width: 27em;
  height: 40em;
  //min-height: 44em;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5px;
  position: relative;
  z-index: 99;
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.2);
    border-radius: 4px;
    position: relative;
    z-index: 99;
    width: 100%;
    height: 100%;
    z-index: 99;
    padding: 17px 10px;
    //transition: transform 200ms ease-in-out;
  }
  .right-side {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 90%;
    background-color: #3498db;
    width: 100%;
    position: absolute;
    right: -34%;
    border-radius: 6px;
    z-index: 1;
    transition: all 400ms ease-in-out;
    cursor: pointer;
    box-shadow: 0px 0px 12px 2px rgba(15, 15, 15, 0.281);

    &.right {
      right: -40%;
      align-items: flex-end;
      &:hover {
        right: -45%;
      }
    }
    &.left {
      right: 40%;
      align-items: flex-start;
      &:hover {
        right: 45%;
      }
    }

    .text {
      font-size: 21px;
      font-weight: 500;
      color: #fff;
      margin-right: 3em;
      margin-left: 3em;
    }
  }
}

//Button
.btn {
  font-size: 21px;
  padding: 5px 20px;
  border: 0;
  background-color: #3498db;
  color: #fff;
  border-radius: 3px;
  transition: all 250ms ease-in-out;
  cursor: pointer;
  &:hover {
    background-color: #2386c8;
  }
  &:focus {
    outline: none;
  }
}
